import React from 'react';
import {Admin, Resource, ListGuesser, EditGuesser, AppBar, UserMenu, MenuItemLink, Layout} from 'react-admin';
import {UserList} from "./components/users";
import { Route } from "react-router";
import Header from "./components/Header";
import {PostCreate, PostEdit, PostList} from "./components/posts";
import authProvider from "./providers/authProvider";
import dataProvider from "./providers/dataProvider";
import Dashboard from "./components/Dashboard";
import {DeviceList,PostEditDevice} from "./components/devices";
import GroupIcon from '@material-ui/icons/Group';
import { createMuiTheme } from '@material-ui/core/styles';
import PostAddIcon from '@material-ui/icons/PostAdd';
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import {TodoCreate, TodoEdit, TodoList} from "./components/todos";
import PhotoAlbumIcon from '@material-ui/icons/PhotoAlbum';
import PhotoSizeSelectActualIcon from '@material-ui/icons/PhotoSizeSelectActual';
import ChatBubbleIcon from '@material-ui/icons/ChatBubble';
import {AlbumCreate, AlbumEdit, AlbumList} from "./components/albums";
import {PhotoCreate, PhotoEdit, PhotoList} from "./components/photos";
import {CommentEdit, CommentList,CommentCreate} from "./components/comments";
import SettingsIcon from '@material-ui/icons/Settings';
import MyUserMenu from './components/MyUserMenu';
import { ProfileEdit,useProfile,ProfileProvider } from "./components/profile";
//import MyLayout from './components/MyLayout';
import ThemedCustomRouteNoLayout from './components/ThemedCustomRouteNoLayout';

const theme = createMuiTheme({
    palette: {
        type: 'light', // Switching the dark mode on is a single property value change.
        secondary: {
            main: "#0C73AE",
        },
    },
});

const MyAppBar = props => <AppBar {...props} userMenu={<MyUserMenu />} />;

const MyLayout = props => <ProfileProvider><Layout {...props} appBar={MyAppBar} /></ProfileProvider>;

function App() {
    return (
        <Admin
            layout={MyLayout}
            theme={theme}
            dashboard={Dashboard}
            dataProvider={dataProvider}
            authProvider={authProvider}
            customRoutes={[
                <Route
                    key="my-profile"
                    path="/my-profile"
                    render={() => <ProfileEdit />}
                />,
                <Route
                    exact
                    path="/custom-no-layout"
                    component={(props) => <ThemedCustomRouteNoLayout {...props} />}
                    noLayout // Skip the layout
                />,
                <Route
                    exact
                    path="/custom"
                    component={(props) => <ThemedCustomRouteNoLayout {...props} />}
                />,
            ]}>
            <Header/>
            <Resource name="devices" list={DeviceList} icon={FormatListBulletedIcon} edit={PostEditDevice}/>
            <Resource name="users" list={UserList} icon={GroupIcon}/>
            <Resource name="profile" list={ListGuesser}/>
        </Admin>
    );
}

export default App;
