const getProfileData = () => {
    const profileObj = {
        id:"1",email: "",
    };
    localStorage.setItem('Email', "test123@email.com");
    profileObj.email = localStorage.getItem('Email');
    const json = JSON.stringify(profileObj);
    return json;
}

export default {getProfileData};